import Toast from '../../wxcomponents/vant/toast/toast';
export default {
	data() {
		return {
			invite: ''
		};
	},
	methods:{
		async onSubmit(){
			await wx.cloud.callFunction({
			  name: 'joinClass',
			  data: {
				  invite: this.invite
			  }
			}).then(res => {
				if(res.result.result == true){
					uni.redirectTo({
					    url: '/pages/success/success'
					})
				}else{
					Toast('邀请码有误')
				}
			}).catch(err => {
			  // handle error
			})
		},
		inputInvite(event){
			this.invite = event.detail
		},
	}
}
